.coding-CreateCoding-Main {
  padding: 20px;
  background: #f1f3f6;
  border-radius: 8px;

  .css-ahj2mt-MuiTypography-root {
    font-family: "Poppins", sans-serif;
  }

  .jodit-container .jodit-workplace {
    background: #fff !important;
  }

  .jodit-toolbar-editor-collection_size_middle.jodit-toolbar-editor-collection_mode_horizontal {
    background: #fff !important;
  }

  .coding-add-question-button {
    background: #fff !important;
    color: #04724d !important;
    border-radius: 4px !important;
    width: 140px !important;
    font-size: 13px !important;
    font-weight: 600;
  }

  .coding-controlled-radio {
    padding: 10px;
  }

  .coding-question-area {
    display: flex;
    justify-content: space-between;
    padding-bottom: 10px;
    align-items: center;
  }

  .autocomplete-option {
    .wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border: 1px solid #dadada;
      border-radius: 4px;
      font-size: 13px;
      font-weight: 500;
      padding: 0px 10px;
      height: 34px;
      background-color: #fff;

      .label-div {
        margin-right: 5px;
      }

      .checkbox-div {
        .css-r6o413-MuiButtonBase-root-MuiCheckbox-root {
          padding: 0;
        }
      }
    }
  }
  .language-completion-area {
    padding-bottom: 10px;
    display: flex;

    .language-options {
      width: 210px;
      margin-right: 20px;

      .options-select {
        padding-bottom: 10px;
      }

      .css-tlfecz-indicatorContainer {
        padding: 0;
      }
      .css-1gtu0rj-indicatorContainer {
        padding: 0;
      }
      .css-g1d714-ValueContainer {
        position: unset;
        height: 100%;
      }
    }
  }

  .coding-editor {
    width: 100%;
  }

  .coding-question-text-area {
    width: 100%;
    border: 2px solid #ddd;
    border-radius: 4px;
    padding: 10px;
    font-size: 13px;
    margin-bottom: 10px;
  }

  .coding-question-min-sec {
    display: flex;
    margin-bottom: 10px;
    .coding-ques-min input {
      width: 60px;
      height: 34px;
      margin-right: 10px;
      text-align: center;
      border: 2px solid #ddd;
      border-radius: 4px;
      font-size: 13px;
    }

    .coding-ques-sec input {
      width: 60px;
      height: 34px;
      font-size: 13px;
      margin-right: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 4px;
      text-align: center;
      border: 2px solid #ddd;
    }
  }

  .multiSelectContainer {
    text-align: left;
    width: 100%;
  }

  .multiSelectContainer .MuiAutocomplete-tag {
    padding: 11px;
    margin: 5px;
  }

  .MuiAutocomplete-input:first-child {
    padding: 6px 10px !important;
    font-size: 13px;
  }
  .MuiAutocomplete-inputRoot .MuiAutocomplete-input {
    font-size: 13px;
  }

  .multiSelectContainer li:hover {
    background: #04724d !important;
    color: #fff;
    cursor: pointer;
  }

  .MuiInput-underline:before {
    border-bottom: none;
  }

  .MuiInput-underline:before {
    border-bottom: none;
  }

  .coding-add-question-button-2 {
    border-radius: 4px !important;
    width: 150px !important;
  }
}

.coding-top-filter-clear {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;

  .coding-filter-by-skils {
    font-size: 13px;
    font-weight: 600;
    margin-right: 10px;
  }

  .coding-clear-filter {
    display: flex;
    align-items: center;

    .coding-close {
      height: 30px;
      width: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }
  }
}

.coding-questionSkills .MuiInputBase-root {
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto !important;
  min-height: 34px !important;
  border: 2px solid #ddd;
  border-radius: 4px;
  background: #fff;
  margin-bottom: 20px;
}

.coding-questionSkills .MuiChip-outlined {
  border: 1px solid #333;
  color: rgb(126, 119, 119);
  background: black !important;
  height: 20px;
  margin: 5px;
}
.MuiChip-outlined .MuiChip-deleteIcon {
  color: #fff;
}

.MuiChip-outlined .MuiChip-deleteIcon:hover {
  color: #fff;
}

